import React from 'react'
import './Header.css';


function Header() {
    return (
        
        <div className='header-container'>
            
            <h1 className='title'>Hi, My Name Is 
            <span className='title-span'> Dean Ward </span>
            And I'm A Front-End Web Developer.
            </h1>
            
            <div className='button-container'>
                <button className='enter'><a className='button-link' href='#about'>Enter Portfolio<i className='fas fa-arrow-down'></i></a></button>
                <button className='git'>  <a className='button-link' href='https://github.com/Deaano'>Github<i className="fab fa-github-square git-square"></i></a></button>
            </div>
        
            
        </div>
    )
}

export default Header;
